@charset 'UTF-8';
* {margin:0; padding:0;}
ul,ol,li {list-style:none;}
a:link, a:visited, a:active {color:#fff; text-decoration:none;}

html {background:#000;overflow:hidden;}

.header_wrap {display:flex; justify-content:space-between; align-items: flex-end; margin:10px 45px;}
.header_wrap > h2 {font-size: 30px;}
.header_wrap > ul {display:flex;}
.header_wrap > ul > li {margin-left:30px; font-size: 20px; font-weight: bold;}


#section {position:relative; background:#fff; margin:0 30px; height: 800px;border-radius:25px;}

.photo_wrap {position:absolute; right:30px; bottom:30px;  }
.photo_wrap .photo {
  position:relative;
  z-index:10; 
  overflow:hidden;
  width:100px; 
  height: 100px; 
  border-radius:100px;
  cursor:pointer;
  background:#000;
}
.photo_wrap .photo > img {
  display: block; 
  width:100%;
}
.photo_wrap .address {
  display:flex; 
  flex-flow:column nowrap; 
  justify-content: center; 
  overflow:hidden; 
  position:absolute; 
  left: 0;
  top: 0; 
  z-index:8; 
  width:100px;
  height: 100px; 
  background:#000; 
  border-radius:100px;
  transition:all 0.4s; 
}
.photo_wrap .address.active {
  left:-270px; 
  width:260px; 
  color:#fff; 
  transition:all 1s;
}
.photo_wrap .address > p {
  width:150px;
  margin:0 auto;
  color:#000; 
  font-size: 20px;
}
.photo_wrap .address.active > p {
  position: relative;
  padding-left: 40px;
  color:#fff;
  margin-bottom: 5px;
  text-align:left;
  word-break:keep-all; 
  white-space: nowrap;
}
.photo_wrap .address.active > p:nth-child(1):before{
  content:'';
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: 0;
  width:27px;
  height: 27px;
  background: url(./images/circle-phone.png)no-repeat center;
  background-size:contain;
}
.photo_wrap .address.active > p:nth-child(2):before{
  content:'';
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: 0;
  width:27px;
  height: 27px;
  background: url(./images/circle-envelope.png)no-repeat center;
  background-size:contain;
}